@import '~antd/dist/antd.css';

.menuBar{
  padding: 0 20px;
  border-bottom: solid 1px #e8e8e8;
  overflow: auto;
  box-shadow: 0 0 30px #f3f1f1;
  background-color: #212121;
  color: white; 
  border-radius: 1.5rem;
}

.linearGradient {
  background-image: linear-gradient(to right bottom, rgb(0, 255, 255), rgb(0, 153, 255));

}

.alignLeft {
  padding-left: 50px;
  text-align: left;
}

.righter {
  padding-left: 30px;
  padding-bottom: 10px;
  font-size: 14px;
}

th {
  height: 70px;
}

label {
  cursor: pointer;
}

tr:nth-child(even) {background-color: #f2f2f2;}

tr {
  height: 60px;
  cursor: pointer;
}

.notFull {
  margin: 0 auto;
  width: 80%;
}

.wht {
  color: white;
}

.imgHeight {
  height: 100rem;
}

.navColor {  
  color: white;
  background-color: #212121;
}

.logo{
  width: 150px;
  float: left;
}
.logo a{
  display: inline-block;
  font-size: 18px;
  font-family: 'Dosis', sans-serif;
  padding: 12px 12px;
  text-transform: capitalize;
}
.menuCon{
  width: calc(100% - 150px);
  float: left;
  color: white;
}

.menuCon .ant-menu-item{
  padding: 0px 5px;
}
.menuCon .ant-menu-submenu-title{
  padding: 10px 20px;
}
.menuCon .ant-menu-item a, .menuCon .ant-menu-submenu-title a{
  padding: 10px 15px;
}
.menuCon .ant-menu-horizontal{
  border-bottom: none;
}
.menuCon .leftMenu{
  float: left;
}
.menuCon .rightMenu{
  float: right;
}
.barsMenu{
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none;
  background: none;
  background-color: #212121;
  color: grey;   
}
.barsBtn{
  display: block;
  width: 20px;
  height: 2px;
  background: #1890ff;
  position: relative;
}
.barsBtn:after,.barsBtn:before{
  content: attr(x);
  width: 20px;
  position: absolute;
  top: -6px;
  left: 0;
  height: 2px;
  background: #1890ff;
}
.barsBtn:after{
  top: auto;
  bottom: -6px;
}
.ant-drawer-body{
  padding: 0;
}
.ant-drawer-body .ant-menu-horizontal > .ant-menu-item, .ant-drawer-body .ant-menu-horizontal > .ant-menu-submenu{
  display: inline-block;
  width: 100%;
}
.ant-drawer-body .ant-menu-horizontal{
  border-bottom: none;
}
.ant-drawer-body .ant-menu-horizontal > .ant-menu-item:hover{
  border-bottom-color: transparent;;
}

@media(max-width: 767px){
  .barsMenu{
    display: inline-block;
  }
  .leftMenu,.rightMenu{
    display: none;
  }
  .logo a{
    margin-left: -20px;
  }
  .menuCon .ant-menu-item, .menuCon .ant-menu-submenu-title{
    padding: 1px 20px;
  }
  .logo a{
    padding: 10px 20px;
  }
}
.App {
  text-align: center;
}

.specimg {
  width: 50%;
  height: 15 vh;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #212121;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  background-color: black;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

